@nav-indexing: 5;
@collapsed-nav-width: 100%;
.nav-left {
  float: none;
  background-color: #f3f3f3;
  >li {
    display: table;
    width: 100%;
    svg,
    img {
      position: absolute;
      top: 50%;
      margin-top: -32px;
      left: 15px;
      width: 62px;
      height: auto;
      fill: @gray-light;
    }
    >a {
      display: table-cell;
      vertical-align: middle;
      width: 100%;
      z-index: @nav-indexing;
      height: 97px;
      background-repeat: no-repeat;
      background-position: 15px center;
      padding: 0 15px 0px 92px;
      color: #565e66;
      font-size: 17px;
      @media (max-width: @screen-sm-max) {
        font-size: 1.2em;
        line-height: 1.1em;
        background-image: none;
        width: @collapsed-nav-width;
        height: 20%;
        display: block;
        margin: 0 !important;
        padding: 8px 0px 6px 15px !important;
      }
      @media (max-width: @screen-xs-max) {
        display: block;
        margin: 0 !important;
        width: 100%;
        background-position: 5% center;
        background-size: 30px 22px;
        height: 20%;
      }
      &:hover,
      &:focus {
        background-color: #00509b;
        color: #fff;
        svg {
          fill: #fff;
        }
        @media (max-width: @screen-md) {
          font-size: 1.2em;
          line-height: 1.1em;
          background-image: none;
        }

      }
    }
    @media (min-width: @screen-md) {
      ul {
        display: none!important;
      }
    }
  }
}

