.slider-tabs {
  a {
    position: relative;
    @media (max-width: @screen-xs-max) {
      display: none;
      top: 241px;
      width: 100%;
      position: absolute;
      height: 20%;
      font-size: 24px;
      margin: 0;
      padding: 20px;
      transition: .3s;
      line-height: 1.8em;
      z-index: 99;
    }
  }
  a.flex-active:after {
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    width: 0;
    height: 0;
    margin-left: -20px;
    content: "";
    display: block;
    position: absolute;
    bottom: 100%;
    left: 50%;
    z-index: 2;
    @media (max-width: @screen-xs-max) {
      display: none;
    }
  }
}

