// main: main.less
#skip-nav {
  top: -30px;
  left: 3px;
  width: auto;
  height: auto;
  padding: 4px 8px;
  z-index: 1070;
  position: absolute;
  background-color: @gray-lighter;
  transition: .3s;
  border-radius: 5px;
  opacity: 0;
  &:focus {
    top: 3px;
    opacity: 1;
  }
}

.accessibility-nav,
.accessability-nav {
  float: right;
  a {
    cursor: pointer;
    text-decoration: none;
    font-size: 16px;
    line-height: 1em;
  }
  #increase-text {}
  #decrease-text {}
  #print-page {}
}

@media (min-width: 992px) {
  .navbar-collapse.collapse {
    width: 224px;
    position: absolute;
  }
}

@media (min-width: 992px) {
  .navbar-collapse.collapse.navbar-internal {
    width: 100%;
    float: none;
    .nav>li {
      width: 20%;
      display: block;
      float: left;
      padding-left: 4px;
    }
  }
}

.nav-bumper {
  margin-top: 53px;
  @media (max-width: @screen-sm-max) {
    margin-top: 0px;
  }
}
